<template>
    <div class="recommend-box">
      <div class="recommend-item" v-for="(item,index) in dataArr.itemData" :key="index">
        <p>{{ item.title }}</p>
        <ul>
          <li v-for="(item,index) in item.imgList">
            <img :src="item.imgSrc" alt="" />
          </li>
        </ul>
        <div class="recommend-bottom">
          <div class="recommend-bottom-lf">
            <span class="status"><em class="iconfont icon-zuanshi" style="font-size:18px;vertical-align:-2px"></em>{{item.zuan}}</span>
            <i>{{ item.author }}</i>
            <span><em class="iconfont icon-yanjing" style="font-size:18px;vertical-align:-2px"></em>{{item.view}}</span>
            <span><em class="iconfont icon-xinxi" style="font-size:18px;vertical-align:-2px"></em>{{item.leavMessage}}</span>
            <span><em class="iconfont icon-shoucang-copy" style="font-size:18px;vertical-align:-2px"></em>{{item.collect}}</span>
          </div>
          <span class="iconfont icon-guanbi recommend-bottom-rt" style="font-size:18px;vertical-align:-2px"
          @click="goDelete(index)"></span>
        </div>
      </div>
  </div>
</template>

<script>
  import Swiper from "../../../../node_modules/swiper";
  export default {
    name: "recommend",
    data() {
      return {
        dataArr: {
          itemData: [
            {
              title:"女孩插画，推荐关键词：女孩、女人、女性、插画",
              author: "霸行者",
              view: 123,
              leavMessage: 456,
              collect: 789,
              zuan: 2.45,
              imgList: [
                {
                  id: 1,
                  // imgSrc: require("../../../assets/images/banner/1.jpg")
                  imgSrc:'https://hellorfimg.zcool.cn/provider_image/preview260/2235589644.jpg'
                },
                {
                  id: 2,
                  imgSrc:'https://image.shutterstock.com/image-photo/-260nw-475963879.jpg'
                },
                {
                  id: 3,
                  imgSrc: 'https://hellorfimg.zcool.cn/provider_image/preview260/2235513988.jpg'
                }
              ]
            },
            {
              title:"女孩插画，推荐关键词：女孩、女人、女性、插画",
              author: "霸行者",
              view: 123,
              leavMessage: 456,
              collect: 789,
              zuan: 2.45,
              imgList: [
                {
                  id: 1,
                  // imgSrc: require("../../../assets/images/banner/1.jpg")
                  imgSrc:'https://hellorfimg.zcool.cn/provider_image/preview260/2235589644.jpg'
                },
                {
                  id: 2,
                  imgSrc:'https://image.shutterstock.com/image-photo/-260nw-475963879.jpg'
                },
                {
                  id: 3,
                  imgSrc: 'https://hellorfimg.zcool.cn/provider_image/preview260/2235513988.jpg'
                }
              ]
            },
            {
              title:"女孩插画，推荐关键词：女孩、女人、女性、插画",
              author: "霸行者",
              view: 123,
              leavMessage: 456,
              collect: 789,
              zuan: 2.45,
              imgList: [
                {
                  id: 1,
                  // imgSrc: require("../../../assets/images/banner/1.jpg")
                  imgSrc:'https://hellorfimg.zcool.cn/provider_image/preview260/2235589644.jpg'
                },
                {
                  id: 2,
                  imgSrc:'https://image.shutterstock.com/image-photo/-260nw-475963879.jpg'
                },
                {
                  id: 3,
                  imgSrc: 'https://hellorfimg.zcool.cn/provider_image/preview260/2235513988.jpg'
                }
              ]
            }
          ]
        }
      };
    },
    methods: {
      goDelete(index) {
        this.dataArr.itemData.splice(index, 1);
      }
    },
    computed: {
      pages() {
        // 大于容器宽度如何显示
        const pages = [];
        this.dataArr.itemData.forEach((item, index) => {
          const page = Math.floor(index / 8);
          // console.log(page)
          console.log(pages);
          if (!pages[page]) {
            pages[page] = [];
          }
          pages[page].push(item);
        });
        return pages;
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import '../../../assets/styles/varibles.styl';
  @import 'http://at.alicdn.com/t/font_989460_nm2th7jnelh.css';
  
  .recommend-box
    padding:57px 15px 45px 15px
    .recommend-item
      padding: 10px 0
      border-bottom: 1px solid #eee
      ul
        width: 100%
        display: flex
        overflow: hidden
        padding: 5px 0 15px 0
        li
          width: 33%
          height: 105px
          margin: 5px
        &:nth-child(2)
          margin: 3px 0 0 0
    p
      font-size: 14px
      line-height: 20px
      font-weight: bold 
      margin-left: 8px
      color:#222
    img
      width: 100%
      height: 100%
      border-radius: 6px
    .recommend-bottom
      display: flex
      justify-content: space-between
      padding: 0 8px 8px 8px
      img
        width: 18px
        height: 18px
        vertical-align: text-bottom
      i
        width: 80px
        text-overflow: ellipsis
        overflow: hidden
        white-space: nowrap
        display: inline-block
        font-style: normal

      .recommend-bottom-lf .status
        color: #EA5D50
</style>



